<template>
  <div class="container ex">
   <div class="title">
      <h1>学校案例</h1>
    </div>
   <div class="content">
     <section>
       <el-carousel :interval="5000" arrow="always" height='350px' >
           <el-carousel-item v-for="item in imgs" >
             <img :src="item.src" alt="">
           </el-carousel-item>
         </el-carousel>
     </section>
   </div>
  </div>
</template>

<script>
  export default{
    data (){
      return {
        item:1,
         imgs:[
           {src:require('@/assets/img/jiudian.jpg')},
           {src:require('@/assets/img/wenquan.jpg')}
           ]
      }
    }
  }
</script>

<style scoped>
.ex{
   padding:20px;
  }
.title{
  display: flex;
  justify-content: center;
  border-bottom:4px solid #dcdcdc;
  height:60px;
  position: relative;
}
.title h1{
  font-weight:400;
  width:190px;
  text-align: center;
  border-bottom: 4px solid #007dfe;
  position: absolute;
  padding-bottom:20px;
  bottom:-4px;
}
.content{
  padding: 30px;
}
section{
  margin: 0 auto;
  width:900px;
}
section img{
  width: 100%;
  height: 100%;
}







</style>



