import Vue from 'vue'
import Router from 'vue-router'
import home from '@/page/index'
import jianjie from '@/page/jianjie'
import index from '@/components/header'
import product from '@/page/product'
import block1 from '@/product/cblock1'
import block2 from '@/product/c_block2'
import success_case from '@/successcase/case'
import card from '@/card/card'
import card_block1 from '@/card/cblock1'
import card_block2 from '@/card/cblock2'
Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component:index,
      children:[
        {
          path: '/',
          name: 'home',
          component:home,
        },
        {
          path: '/jianjie',
          name: 'jianjie',
          component:jianjie
        },
        {
          path: '/product',
          name: 'product',
          component:product,
          children:[{
            path: '/',
            name: 'block1',
            component:block1
          },
          {
            path: 'block2',
            name: 'block2',
            component:block2
          }
          ]
        },
        {
          path: '/case',
          name: 'case',
          component:success_case,
        },
        {
          path: '/card',
          name: 'card',
          component:card,
          children:[
            {
            path: '/',
            name: 'card_block1',
            component:card_block1
          },
          {
            path: 'card_block1',
            name: 'card_block1',
            component:card_block1
          }
          ]
        },
      ]
    }
  ]
})



