<template>
  <div>
     <banner></banner>
     <div class="container jianjie">
       <ul class="tabs">
       	  <li @click="tab(index)" v-for="(item,index) in lis" :class="[{active:index==tindex}]" >{{item}}</li>
       </ul>
        <component :is='com'></component>
     </div>
     <foot></foot>
  </div>
</template>

<script>
   import banner from '@/components/banner'
   import foot from '@/components/foot'
   import blokc1 from '@/jianjie/block1'
   import blokc2 from '@/jianjie/block2'
   import blokc3 from '@/jianjie/block3'
   import blokc4 from '@/jianjie/block4'
   export default {
     components:{banner,blokc1,blokc2,blokc3,blokc4,foot},
     data (){
       return {
         tindex:0,
         tabs:['blokc1','blokc2','blokc3','blokc4'],
         com:'blokc1',
         lis:['分部概况','组织结构','经营理念','荣誉资质']
       }
     },
     methods:{
       tab(index){
         this.tindex=index;
         this.com=this.tabs[index];
       }
     }
   }
</script>

<style scoped>
 .jianjie{
     display: flex;
     margin-top: 30px;
  }
.tabs{
  width: 160px;
}
.tabs  li{
  height:60px;
  line-height:60px;
  text-align: center;
  border: 1px solid  #dcdcdc;
  /* border-left: 1px solid #dcdcdc;
  border-right: 1px solid #dcdcdc;
  border-bottom: 1px solid #dcdcdc; */
  cursor: pointer;
}
.tabs li.active{
  background: #007DFE;
  color: #fff;
}

</style>



