<template>
  <header>
  <nav class="container">
          <img src="../assets/img/logo.png" alt=""/>
              <ul>
                <li v-for="item in navs">
                   <router-link :to='item.url' :class="[{active:item.url==$route.path}]">
                     {{item.text}}
                   </router-link>
                 </li>
              </ul>
      </nav>
      <router-view></router-view>
  </header>
</template>

<script>
 export default{
   data (){
     return {
        navs:[
          {
          text:'首页',
          url:'/'
        },
        {
          text:'分部简介',
          url:'/jianjie'
        },
        {
          text:'产品简介',
          url:'/product'
        },
        {
          text:'制卡介绍',
          url:'/card'
        },
        {
          text:'成功案例',
          url:'/case'
        },
        {
          text:'下载中心',
          url:''
        },
        {
          text:'联系我们',
          url:''
        }
        ]
     }
   }
 }
</script>

<style scoped>


nav{
    display: flex;
    align-items: center;
	   height: 70px;
	  justify-content: space-between;
}
nav img{
    width: 170px;
    height:50px;
}
nav ul {
    display: flex;
}
nav ul li a.active{
  background: #36c7d1;
  color: #fff;
}
nav ul li a{
    display: inline-block;
    width: 130px;
    height: 70px;
    line-height: 70px;
    text-align: center;
    color: #666;
    transition: .3s;
}
nav ul li a:hover{
    color: #fff;
    background: #36c7d1;
}













</style>



