<template>
  <div>
    <el-carousel :interval="5000" arrow="always" height='400px' :key="item">
        <el-carousel-item v-for="item in imgs" >
          <img :src="item.src" alt="">
        </el-carousel-item>
      </el-carousel>
  </div>
</template>

<script>
  export default{
    name:'banner',
    data (){
      return {
        item:1,
         imgs:[
           {src:require('@/assets/img/jiudian.jpg')},
           {src:require('@/assets/img/wenquan.jpg')}
           ]
      }
    }
  }
</script>

<style scoped>

  .el-carousel__item img{
    width: 100%;
    height: 100%;
  }
</style>



