<template>
  <div class="container newslist">
    <div class="title">
      <h1>公司简介</h1>
    </div>
     <section>
       <ul class="tab">
         <li @click="tab(index)" v-for="(item,index) in tabs" :class="[{active:index==tindex}]" >{{item}}</li>
       </ul>
        <component :is="com"></component>
     </section>
  </div>
</template>

<script>
  import qiyenews from './qiyenews.vue'
  import new2 from './new2'
  import new3 from './new3'
  export default{
    components:{qiyenews,new2,new3},
    data (){
      return {
         tindex:0,
         tabs:['企业要闻','公司公告','行业动态'],
         com:'qiyenews',
         content:['qiyenews','new2','new3']
      }
    },
    methods:{
      tab(index){
        console.log(index)
        this.tindex=index;
        this.com=this.content[index];
      }
    }
  }
</script>

<style scoped>
 .newslist{
   padding:20px;
  }
.title{
  display: flex;
  justify-content: center;
  border-bottom:4px solid #dcdcdc;
  height:60px;
  position: relative;
}
.title h1{
  font-weight:400;
  width:190px;
  text-align: center;
  border-bottom: 4px solid #007dfe;
  position: absolute;
  padding-bottom:20px;
  bottom:-4px;
}

.tab{
  display: flex;
  margin-top:30px;
  justify-content:center;
}
.tab li{
  width:200px;
  height:40px;
  background: #f2f1f7;
  margin-right: 10px;
  line-height: 40px;
  text-align: center;
  cursor: pointer;
}
.tab li.active{
   background: #e97913;
   color:#fff;
}






</style>



